.weather-app-container {
    background: url('https://source.unsplash.com/1920x1080/?weather') center/cover;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff; 
  }
  
.input-text {
    padding: 10px;
    margin-bottom: 10px;
    font-size: 16px;
}

.submit-button {
    padding: 12px 20px;
    font-size: 18px;
    background-color: #4caf50;
    color: white;
    border: none;
    cursor: pointer;
}

.submit-button:disabled {
    background-color: #a0a0a0;
    cursor: not-allowed;
}

.loading-spinner {
    border: 6px solid #f3f3f3;
    border-top: 6px solid rgb(21, 124, 0);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    animation: spin 1s linear infinite;
}

.weather-forecast {
    text-align: center; /* Center-align the text within the weather-forecast */
    margin: 0 auto; /* Set left and right margins to 'auto' for centering */
    background-color: rgb(255, 255, 255); /* Add a light background color for better visibility */
    padding: 20px; /* Add padding for spacing */
    border-radius: 8px; /* Add border radius for a rounded appearance */
    margin-top: 20px; /* Add margin to separate it from the input and button */
  }

.weather-forecast h2 {
    color: #3498db;
    /* Set the color to a vibrant blue */
    font-size: 24px;
}

.weather-forecast li {
    list-style-type: none;
    margin-bottom: 10px;
    font-size: 18px;
    color: #0f0f0f;
}

.weather-forecast li strong {
    color: #e74c3c;
    /* Set the color of the date to a vibrant red */
}

.weather-forecast ul {
    list-style-type: disc; /* Show bullet points */
    padding-left: 20px; /* Add some left padding for better spacing */
    color: #0f0f0f;
  }

.error-message {
    color: red;
    font-size: 16px;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}